<template>
  <v-dialog v-model="displayDialog" max-width="500" @keydown.esc="close">
    <v-card>
      <v-card-title>Om Fruktkartan</v-card-title>
      <v-card-text>
        <p>
          Fruktkartan r en ppen databas med frukttrd p allmnningar och i
          parker. Vem som helst kan lgga till nya trd, och redigera eller ta
          bort sdana som inte passar hr. Sajten r byggd av Leo Wallentin,
          Matti Ryhnen och Daniel Lublin. Fruktikoner av Pedro Loureiro.
        </p>

        <p>
          Andra sajter och appar kan anvnda information hrifrn, via ett ppet
          API. Informationen r licenserad med
          <a
            href="https://opendatacommons.org/licenses/odbl/summary/"
            target="_blank"
            rel="noopener"
            >Open Database License</a
          >, bilder med
          <a
            href="https://creativecommons.org/publicdomain/zero/1.0/deed.sv"
            target="_blank"
            rel="noopener"
            >CC0</a
          >.
        </p>
        <p>
          Fruktkartan r byggd med Vue/Vuetify samt Leaflet med Open Street Maps
          kartor. Kllkoden r fri, <em>copyleft</em> och licenserad med
          <a
            href="https://www.gnu.org/licenses/agpl-3.0.html"
            target="_blank"
            rel="noopener"
            >AGPLv3</a
          >.
        </p>
        <p>
          Har du hittat en bugg? Rapportera det
          <a
            href="https://github.com/fruktkartan/fruktkartan/issues?q=is%3Aissue+is%3Aopen+sort%3Aupdated-desc"
            target="_blank"
            rel="noopener"
            >p Github!</a
          ><br />
          Frgor? Skicka ett
          <a href="mailto:mejl@leowallentin.se">mejl</a>!
        </p>
        <small>
          Byggd {{ timestamp }} frn
          <a :href="gitcommit" target="_blank" rel="noopener">{{ githash }}</a
          >.
        </small>
      </v-card-text>
      <v-card-actions>
        <v-btn @click.stop="close"> Stng </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "AboutUs",
  props: {
    value: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      timestamp: process.env.VUE_APP_TIMESTAMP ?? "?",
      githash: process.env.VUE_APP_GITHASH ?? "?",
    }
  },
  computed: {
    gitcommit: function () {
      return "https://github.com/fruktkartan/fruktkartan/commit/" + this.githash
    },
    displayDialog: {
      get: function () {
        return this.value ? true : false
      },
      set: function (val) {
        this.$emit("input", val)
      },
    },
  },
  methods: {
    close() {
      this.$emit("input", false)
    },
  },
}
</script>
